import request from '@/utils/request'
// 添加问题
export function addcq (data) {
  return request({
    url: '/question/addcq',
    method: 'post',
    data: data
  })
}
// 全部问题
export function getAll () {
  return request({
    url: '/question/allquestion',
    method: 'get'
  })
}
// 随机问题
export function getRand () {
  return request({
    url: '/question/randquestion',
    method: 'get'
  })
}
// 修改问题
export function editcq (data) {
  return request({
    url: '/question/updatecq',
    method: 'post',
    data: data
  })
}
// 删除问题
export function deletecq (id) {
  return request({
    url: '/question/deletecq?id=' + id,
    method: 'get'
  })
}
