  <div class="content">
    <el-tabs type="card">
      <el-tab-pane>
        <span slot="label"><i class="el-icon-edit-outline"></i> 试题编辑</span>
        <div class="add-issue-btn">
          <el-button type="warning" @click="dialogVisible = true"
            >添加试题</el-button
          >
        </div>
        <!-- 选择题列表 -->
        <!-- <span class="question-type">选择题</span> -->
        <h3 class="color-main" style="text-align: center">选择题</h3>

        <el-card v-for="(question, i) in question_choice" :key="question.id">
          <el-button
            class="dlt-btn"
            size="mini"
            type="danger"
            @click="deleteCQ(question.id, i)"
            >删除</el-button
          >
          <i>
            <el-button
              v-if="question.isEditable"
              size="mini"
              type="success"
              @click="updateCQ(question)"
              >确认</el-button
            >
            <el-button
              v-else
              size="mini"
              type="primary"
              @click="question.isEditable = 1"
              >修改</el-button
            >
          </i>
          <strong
            >{{ i + 1 }}.
            <span v-if="!question.isEditable">{{ question.title }}</span>
            <el-input
              v-else
              v-model="question.title"
              style="width: 80%"
            ></el-input> </strong
          ><br />
          <el-row>
            <span v-for="(option, index) in question.options" :key="index">
              <el-checkbox
                :disabled="!question.isEditable ? true : false"
                v-model="option.value"
                >{{ String.fromCharCode(97 + index) }}.
                {{ option.dec }}</el-checkbox
              >
            </span>
          </el-row>
        </el-card>
        <!-- 问答题列表 -->
        <!-- <span class="question-type">问答题</span> -->
        <!--
        <h3 class="color-main" style="text-align: center">问答题</h3>
        <el-card
          v-for="(question, i) in question_essay"
          :key="question.id + 'eq'"
        >
          <el-button
            class="dlt-btn"
            size="mini"
            type="danger"
            @click="deleteEQ(question.id, i)"
            >删除</el-button
          >
          <i>
            <el-button
              v-if="question.isEditable"
              size="mini"
              type="success"
              @click="updateEQ(question)"
              >确认</el-button
            >
            <el-button
              v-else
              size="mini"
              type="primary"
              @click="question.isEditable = 1"
              >修改</el-button
            >
          </i>
          <strong
            >{{ i + 1 }}.
            <span v-if="!question.isEditable">{{ question.title }}</span>
            <el-input
              v-else
              v-model="question.title"
              style="width: 80%"
            ></el-input> </strong
          ><br />
          <el-row data-name="textarea">
            <span v-if="!question.isEditable">{{ question.answer }}</span>
            <el-input
              v-else
              v-model="question.answer"
              type="textarea"
              :rows="6"
              placeholder="请输入内容"
            ></el-input>
          </el-row>
        </el-card>
        -->
        <!-- 添加问题 -->
        <el-dialog
          title="添加试题"
          :visible.sync="dialogVisible"
          width="70%"
          :before-close="handleClose"
        >
          <el-select
            v-model="questionType"
            style="width: 100px; margin: 0 45% 10px"
          >
            <el-option label="选择题" value="cq"></el-option>
            <el-option label="问答题" value="eq"></el-option>
          </el-select>
          <el-card v-if="questionType === 'cq'" v-loading="uploadLoading">
            <el-form
              :model="questionForm"
              ref="questionForm"
              label-width="100px"
            >
              <el-form-item
                prop="title"
                label="问题题目"
                :rules="{
                  required: true,
                  message: '题目不能为空',
                  trigger: 'blur',
                }"
              >
                <el-input
                  v-model="questionForm.title"
                  style="width: 80%"
                ></el-input>
              </el-form-item>
              <el-form-item
                v-for="(option, index) in questionForm.options"
                :label="'选项 ' + String.fromCharCode(97 + index)"
                :key="index"
                :prop="option.dec"
              >
                <div class="option-value-item">
                  <el-checkbox v-model="option.value"></el-checkbox>
                </div>
                <el-input v-model="option.dec" style="width: 70%"></el-input>
                <el-button type="danger" @click.prevent="removeOption(index)"
                  >删除</el-button
                >
              </el-form-item>
              <el-form-item>
                <el-button @click="resetFormCQ">重置</el-button>
                <el-button type="success" @click="addOption"
                  >增加选项</el-button
                >
                <el-button type="primary" @click="submitForm('questionForm')"
                  >提交</el-button
                >
              </el-form-item>
            </el-form>
          </el-card>
          <el-card v-if="questionType === '之后看需求是否添加'" v-loading="uploadLoading">
            <el-form
              :model="questionEqForm"
              ref="questionEqForm"
              label-width="100px"
            >
              <el-form-item
                prop="title"
                label="题目"
                :rules="{
                  required: true,
                  message: '题目不能为空',
                  trigger: 'blur',
                }"
              >
                <el-input
                  v-model="questionEqForm.title"
                  style="width: 80%"
                ></el-input>
              </el-form-item>
              <el-form-item
                prop="answer"
                label="答案"
                :rules="{
                  required: true,
                  message: '答案不能为空',
                  trigger: 'blur',
                }"
              >
                <el-input
                  v-model="questionEqForm.answer"
                  type="textarea"
                  :rows="6"
                  style="width: 80%"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button @click="resetForm('questionEqForm')">重置</el-button>
                <el-button
                  type="primary"
                  @click="submitFormEQ('questionEqForm')"
                  >提交</el-button
                >
              </el-form-item>
            </el-form>
          </el-card>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
          </span>
        </el-dialog>
      </el-tab-pane>
      <!--     <el-tab-pane>
        <span slot="label"><i class="el-icon-edit-outline"></i> 问答题</span>
      </el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
import axios from 'axios'
import {
  addcq, getAll, editcq, deletecq
} from '@/api/question'
export default {
  data () {
    return {
      // 选择题
      question_choice: [],
      // 问答题
      question_essay: [],
      // 加载读条
      uploadLoading: false,
      // 提交的选择题
      questionForm: {
        title: '',
        options: [
          { dec: '', value: false },
          { dec: '', value: false },
          { dec: '', value: false },
          { dec: '', value: false }
        ],
        isEditable: 0
      },
      // 提交的问答题
      questionEqForm: {
        title: '',
        answer: '',
        isEditable: 0
      },
      dialogVisible: false,
      questionType: 'cq',
      userHabbit: false
    }
  },
  methods: {
    // 获取所有问题
    async getQuestions () {
      const data = await getAll()
      this.question_choice = data.question_choice
      this.question_essay = data.question_essay
    },
    // 修改问题
    async updateCQ (question) {
      if (question.options.some((val) => val.value === true)) {
        const data = await editcq(question)
        if (!data.result.length) {
          question.isEditable = 0
          this.$message.success('已修改！')
        } else {
          this.$message.error('服务器出现未知错误，请稍后再试！')
        }
      } else {
        this.$alert('请设置至少一个正确选项！')
      }
    },
    async updateEQ (question) {
      question.answer.trim()
      if (question.title.length && question.answer.length) {
        const { data } = await axios.post('/api/question/updateeq', question)
        if (!data.result.length) {
          question.isEditable = 0
          this.$message.success('已修改！')
        } else {
          this.$message.error('服务器出现未知错误，请稍后再试！')
        }
      } else {
        this.$alert('不能为空！')
      }
    },
    // 删除问题
    deleteCQ (id, i) {
      this.$confirm('即将删除该试题, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const data = await deletecq(id)
          if (!data.result.length) {
            this.$message.success('已删除！')
            this.question_choice.splice(i, 1)
          } else {
            this.$message.error('服务器出现未知错误，请稍后再试！')
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    deleteEQ (id, i) {
      this.$confirm('即将删除该试题, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data } = await axios.get('/api/question/deleteeq?id=' + id)
          if (!data.result.length) {
            this.$message.success('已删除！')
            this.question_essay.splice(i, 1)
          } else {
            this.$message.error('服务器出现未知错误，请稍后再试！')
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 提交新问题
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.questionForm.options.some((val) => val.value === true)) {
            this.uploadLoading = true
            const data = await addcq(this.questionForm)
            if (!data.length) {
              this.$message.success('提交成功！')
              this.getQuestions()
              this.resetFormCQ()
              this.uploadLoading = false
              this.dialogVisible = false
            } else {
              this.$message.error(
                '服务器出现不可预知的错误，若多次出现此问题，请联系管理员解决！'
              )
              this.uploadLoading = false
            }
          } else {
            this.$alert('请设置至少一个正确选项！')
            return false
          }
        } else {
          this.$alert('题目不能为空！')
          return false
        }
      })
    },
    submitFormEQ (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.uploadLoading = true
          const { data } = await axios.post(
            '/api/question/addeq',
            this.questionEqForm
          )
          if (!data.length) {
            this.$message.success('提交成功！')
            this.getQuestions()
            this.resetForm(formName)
            this.uploadLoading = false
            this.dialogVisible = false
          } else {
            this.$message.error(
              '服务器出现不可预知的错误，若多次出现此问题，请联系管理员解决！'
            )
            this.uploadLoading = false
          }
        } else {
          this.$alert('题目不能为空！')
          return false
        }
      })
    },
    // 重置表单
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    resetFormCQ () {
      this.questionForm.title = ''
      this.questionForm.options.map((val) => {
        val.dec = ''
        val.value = false
      })
    },
    // 删除选项
    removeOption (index) {
      this.questionForm.options.splice(index, 1)
    },
    // 增加选项
    addOption () {
      this.questionForm.options.push({ dec: '', value: false })
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(() => {
          done()
        })
        .catch((err) => err)
    }
  },
  created () {
    this.getQuestions()
  }
}
</script>

